import React, { useEffect, useState } from 'react'
import { Route } from 'react-router-dom'
import PreviewPlanets from './previewPlanets'
import Planet from './planet'

const Planets = (props) => {
    const [planets, setPlanets] = useState({ "results": [] });
    const [pageNumber, setPageNumber] = useState(1);
    useEffect(() => {
        const getPlanets = async () => {
            var url = `https://swapi.co/api/planets/?page=${pageNumber}`
            const resp = await fetch(url);
            const data = await resp.json();
            setPlanets(data);
        }
        getPlanets();
    }, [pageNumber]);

    const nextPage = () => {
        console.log(pageNumber)
        if (planets.next != null) {
            setPageNumber(pageNumber + 1)
        }
    }
    const previousPage = () => {
        if (planets.previous != null) {
            setPageNumber(pageNumber - 1)
        }
    }

    let planet = planets.results.map((thisPlanet, i) => {
        return (
            <Route path={`/Planets/${thisPlanet.name}`} key={`${thisPlanet.name}-Page`} render={() => <Planet info={thisPlanet} i={i + 2} pageNumber={pageNumber} />} />
        )
    });


    return (
        <div className="Wrapper">
            <Route exact path="/Planets">
                <div className="planets">
                    <PreviewPlanets planets={planets.results} pageNumber={pageNumber} />
                </div>
                <div className="pageButtons">
                    <button onClick={previousPage}>Previous Page</button>
                    <button onClick={nextPage}>Next Page</button>
                </div>
            </Route>
            {planet}
        </div>
    )
}

export default Planets
