import React from 'react'
import { Link } from 'react-router-dom'

const PreviewPlanets = (props) => {
    console.log(props)
    return (
        <React.Fragment>
            {props.planets.map((planet, i) => {
                return (
                    <div className="planets Preview" key={`Planet-${planet.name}`}>
                        <Link to={`/Planets/${planet.name}`}>
                            <h3>{planet.name}</h3>
                            <img alt={planet.name} src={`https://starwars-visualguide.com/assets/img/planets/${i+2+((props.pageNumber-1)*10)}.jpg`} />
                        </Link>
                    </div>
                )
            })}
        </React.Fragment>
    )
}

export default PreviewPlanets
