import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'

const PlanetList = (props) => {
    console.log(props)
    const [planet, setPlanet] = useState({ "name": "n/a" });
    useEffect(() => {
        const getPlanet = async () => {
            var url = props.planet
            const resp = await fetch(url);
            const data = await resp.json();
            setPlanet(data);
        }
        getPlanet();
    }, [props.planet]);
    if (props.planet !== null) {
        return (
            <div className="planet">
                <Link to={`/Planets/${planet.name}`}>
                    <div className="planet mini">
                        <img alt={planet.name} src={`https://starwars-visualguide.com/assets/img/planets/${props.planet.split("/")[5]}.jpg`} />
                    </div>
                </Link>
            </div>
        )
    } else {
        return (<p>unknown</p>)
    }
}
export default PlanetList
 