import React, { useEffect, useState } from 'react'
import Preview from './preview'
import { Route } from 'react-router-dom'
import Character from "./character"

const Characters = () => {
    const [characters, setCharaceters] = useState({ "results": [] });
    const [pageNumber, setPageNumber] = useState(1);
    useEffect(() => {
        const getCharacters = async () => {
            var url = `https://swapi.co/api/people/?page=${pageNumber}`
            const resp = await fetch(url);
            const data = await resp.json();
            setCharaceters(data);
            console.log(data);
        }
        getCharacters();
    }, [pageNumber]);

    let character = characters.results.map((thisCharacter,i) => {
        return (
            <Route path={`/Characters/${thisCharacter.name}`} key={`${thisCharacter.name}-Page`} render={() => <Character info={thisCharacter} i={i} pageNumber={pageNumber} />} />
        )
    });


    const nextPage = () => {
        console.log(pageNumber)
        if (characters.next != null) {
            setPageNumber(pageNumber + 1)
        }
    }
    const previousPage = () => {
        if (characters.previous != null) {
            setPageNumber(pageNumber - 1)
        }
    }

    return (
        <div className="Wrapper">
            <Route exact path="/Characters">
                <div className="characters">
                    {characters.results.map((character, i) => {
                        let cont = ((pageNumber - 1) * 10) + (i + 1);
                        if (cont >= 17) {
                            cont += 1;
                            if (cont >= 34) {
                                cont += 1;
                            }
                            if (cont >= 46) {
                                cont += 1;
                            }
                            if (cont === 75) {
                                cont = 47;
                            }
                            if (cont >= 75) {
                                cont -= 1;
                            }
                            if (cont === 89) {
                                cont = 35;
                            }
                        }
                        return (
                            <Preview
                                key={character.name}
                                info={character}
                                name={character.name}
                                number={cont}
                            />
                        )
                    })}
                </div>
                <div className="pageButtons">
                    <button onClick={previousPage}>Previous Page</button>
                    <button onClick={nextPage}>Next Page</button>
                </div>
            </Route>
            {character}
        </div>
    )
}

export default Characters
