import React, { useEffect, useState } from 'react'
import Previewvehicles from './previewVehicles'
import { Route } from 'react-router-dom'
import Vehicle from './vehicle'

const Vehicles = () => {
    const [vehicles, setVehicles] = useState({ "results": [] });
    const [pageNumber, setPageNumber] = useState(1);
    useEffect(() => {
        const getVehicles = async () => {
            var url = `https://swapi.co/api/vehicles/?page=${pageNumber}`
            const resp = await fetch(url);
            const data = await resp.json();
            setVehicles(data);
            console.log(data);
        }
        getVehicles();
    }, [pageNumber]);

    let vehicle = vehicles.results.map((thisVehicle,i) => {
        return (
            <Route path={`/Vehicles/${thisVehicle.name}`} key={`${thisVehicle.name}-Page`} render={() => <Vehicle info={thisVehicle}/>} />
        )
    });


    const nextPage = () => {
        if (vehicles.next != null) {
            setPageNumber(pageNumber + 1)
        }
    }
    const previousPage = () => {
        if (vehicles.previous != null) {
            setPageNumber(pageNumber - 1)
        }
    }

    return (
        <div className="Wrapper">
            <Route exact path="/Vehicles">
                <div className="vehicles">
                    {vehicles.results.map((vehicle, i) => {
                        console.log(vehicle)
                        return (
                            <Previewvehicles
                                key={vehicle.name}
                                info={vehicle}
                                id={vehicle.url.split("/")[5]}
                            />
                        )
                    })}
                </div>
                <div className="pageButtons">
                    <button onClick={previousPage}>Previous Page</button>
                    <button onClick={nextPage}>Next Page</button>
                </div>
            </Route>
            {vehicle}
        </div>
    )
}

export default Vehicles
