import React, { useEffect, useState } from 'react'
import PreviewStarShips from './previewStarShips'
import { Route } from 'react-router-dom'
import StarShip from "./starShip"

const StarShips = () => {
    const [StarShips, setStarShips] = useState({ "results": [] });
    const [pageNumber, setPageNumber] = useState(1);
    useEffect(() => {
        const getStarShips = async () => {
            var url = `https://swapi.co/api/starships/?page=${pageNumber}`
            const resp = await fetch(url);
            const data = await resp.json();
            setStarShips(data);
            console.log(data);
        }
        getStarShips();
    }, [pageNumber]);

    let starShip = StarShips.results.map((thisStarShip,i) => {
        return (
            <Route path={`/Spaceships/${thisStarShip.name}`} key={`${thisStarShip.name}-Page`} render={() => <StarShip info={thisStarShip}/>} />
        )
    });


    const nextPage = () => {
        if (StarShips.next != null) {
            setPageNumber(pageNumber + 1)
        }
    }
    const previousPage = () => {
        if (StarShips.previous != null) {
            setPageNumber(pageNumber - 1)
        }
    }

    return (
        <div className="Wrapper">
            <Route exact path="/Spaceships">
                <div className="StarShips">
                    {StarShips.results.map((spaceShips, i) => {
                        console.log(spaceShips)
                        return (
                            <PreviewStarShips
                                key={spaceShips.name}
                                info={spaceShips}
                                id={spaceShips.url.split("/")[5]}
                            />
                        )
                    })}
                </div>
                <div className="pageButtons">
                    <button onClick={previousPage}>Previous Page</button>
                    <button onClick={nextPage}>Next Page</button>
                </div>
            </Route>
            {starShip}
        </div>
    )
}

export default StarShips
