import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'

const StarShipList = (props) => {
    console.log(props);
    const [starShip, setStarShip] = useState({ "results": [] });
    useEffect(() => {
        const getStarShip = async () => {
            var url = props.starShip
            const resp = await fetch(url);
            const data = await resp.json();
            setStarShip(data);
        }
        getStarShip();
    }, [props.starShip]);
    return (
        <div className="starShip">
            <Link to={`/Spaceships/${starShip.name}`}>
                <div className="starship mini">
                    {/* <h3>{starShip.title}</h3> */}
                    <img alt={starShip.name} src={`https://starwars-visualguide.com/assets/img/starships/${props.starShip.split("/")[5]}.jpg`} />
                </div>
            </Link>
        </div>
    )
}
export default StarShipList
