import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'

const VehiclesList = (props) => {
    console.log(props);
    const [vehicles, setVehicles] = useState({ "results": [] });
    useEffect(() => {
        const getVehicles = async () => {
            var url = props.vehicles
            const resp = await fetch(url);
            const data = await resp.json();
            setVehicles(data);
        }
        getVehicles();
    }, [props.vehicles]);
    return (
        <div className="vehicles">
            <Link to={`/vehicles/${vehicles.name}`}>
                <div className="vehicles mini">
                    {/* <h3>{vehicles.title}</h3> */}
                    <img alt={vehicles.name} src={`https://starwars-visualguide.com/assets/img/vehicles/${props.vehicles.split("/")[5]}.jpg`} />
                </div>
            </Link>
        </div>
    )
}
export default VehiclesList
