import React, { useEffect, useState } from 'react'
import PreviewSpecies from './previewSpecies'
import { Route } from 'react-router-dom'
import Specie from './specie'

const Species = () => {
    const [species, setSpecies] = useState({ "results": [] });
    const [pageNumber, setPageNumber] = useState(1);
    useEffect(() => {
        const getSpecies = async () => {
            var url = `https://swapi.co/api/species/?page=${pageNumber}`
            const resp = await fetch(url);
            const data = await resp.json();
            setSpecies(data);
            console.log(data);
        }
        getSpecies();
    }, [pageNumber]);

    let specie = species.results.map((thisspecie,i) => {
        return (
            <Route path={`/Species/${thisspecie.name}`} key={`${thisspecie.name}-Page`} render={() => <Specie info={thisspecie}/>} />
        )
    });


    const nextPage = () => {
        if (species.next != null) {
            setPageNumber(pageNumber + 1)
        }
    }
    const previousPage = () => {
        if (species.previous != null) {
            setPageNumber(pageNumber - 1)
        }
    }

    return (
        <div className="Wrapper">
            <Route exact path="/Species">
                <div className="species">
                    {species.results.map((specie, i) => {
                        console.log(specie)
                        return (
                            <PreviewSpecies
                                key={specie.name}
                                info={specie}
                                id={specie.url.split("/")[5]}
                            />
                        )
                    })}
                </div>
                <div className="pageButtons">
                    <button onClick={previousPage}>Previous Page</button>
                    <button onClick={nextPage}>Next Page</button>
                </div>
            </Route>
            {specie}
        </div>
    )
}

export default Species
