import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'

const FilmList = (props) => {
    const [film, setFilm] = useState({ "results": [] });
    const order = {
        1: 4, 2: 5, 3: 6, 4: 1, 5: 2, 6: 3, 7: 7
    };
    useEffect(() => {
        const getFilm = async () => {
            var url = props.film
            const resp = await fetch(url);
            const data = await resp.json();
            setFilm(data);
        }
        getFilm();
    }, [props.film]);
    return (
        <div className="film">
            <Link to={`/Films/${film.title}`}>
                <div className="mini">
                    {/* <h3>{film.title}</h3> */}
                    <img alt={film.title} src={`https://starwars-visualguide.com/assets/img/films/${order[film.episode_id]}.jpg`} />
                </div>
            </Link>
        </div>
    )
}
export default FilmList
